import { create } from 'zustand';
import { persist } from 'zustand/middleware';

export interface InventoryItem {
  id: string;
  name: string;
  buyPrice: number;
  sellPrice: number;
  supplierName: string;
  expiryDate: string;
  manufacturingDate: string;
  batchNo: string;
  unitType: string;
  categoryType: string;
  quickNote: string;
  stockQuantity: number;
  lowStockThreshold: number;
  createdAt: string;
  updatedAt: string;
}

export interface Category {
  id: string;
  name: string;
  description?: string;
}

export interface Unit {
  id: string;
  name: string;
  abbreviation: string;
}

interface InventoryState {
  items: InventoryItem[];
  categories: Category[];
  units: Unit[];
  defaultLowStockThreshold: number;
  
  // Item operations
  addItem: (item: Omit<InventoryItem, 'id' | 'createdAt' | 'updatedAt'>) => void;
  updateItem: (id: string, updates: Partial<InventoryItem>) => void;
  deleteItem: (id: string) => void;
  getItem: (id: string) => InventoryItem | undefined;
  
  // Stock operations
  updateStock: (id: string, quantity: number, operation: 'add' | 'subtract' | 'set') => void;
  
  // Category operations
  addCategory: (category: Omit<Category, 'id'>) => void;
  updateCategory: (id: string, updates: Partial<Category>) => void;
  deleteCategory: (id: string) => void;
  
  // Unit operations
  addUnit: (unit: Omit<Unit, 'id'>) => void;
  updateUnit: (id: string, updates: Partial<Unit>) => void;
  deleteUnit: (id: string) => void;
  
  // Analytics
  getLowStockItems: () => InventoryItem[];
  getExpiredItems: () => InventoryItem[];
  getNearExpiryItems: (days?: number) => InventoryItem[];
  getLowStockCount: () => number;
  getExpiredCount: () => number;
  getNearExpiryCount: (days?: number) => number;
  getTotalValue: () => number;
  
  // Settings
  setDefaultLowStockThreshold: (threshold: number) => void;
}

export const useInventoryStore = create<InventoryState>()(
  persist(
    (set, get) => ({
      items: [],
      categories: [
        { id: '1', name: 'Medicine', description: 'Pharmaceutical products' },
        { id: '2', name: 'Supplements', description: 'Health supplements' },
        { id: '3', name: 'Personal Care', description: 'Personal hygiene products' },
        { id: '4', name: 'Medical Equipment', description: 'Medical devices and equipment' },
      ],
      units: [
        { id: '1', name: 'Pieces', abbreviation: 'pcs' },
        { id: '2', name: 'Bottles', abbreviation: 'btl' },
        { id: '3', name: 'Boxes', abbreviation: 'box' },
        { id: '4', name: 'Strips', abbreviation: 'strip' },
        { id: '5', name: 'Tablets', abbreviation: 'tab' },
        { id: '6', name: 'Capsules', abbreviation: 'cap' },
      ],
      defaultLowStockThreshold: 10,
      
      addItem: (itemData) => {
        const newItem: InventoryItem = {
          ...itemData,
          id: Date.now().toString(),
          createdAt: new Date().toISOString(),
          updatedAt: new Date().toISOString(),
        };
        set((state) => ({ items: [...state.items, newItem] }));
      },
      
      updateItem: (id, updates) => {
        set((state) => ({
          items: state.items.map((item) =>
            item.id === id
              ? { ...item, ...updates, updatedAt: new Date().toISOString() }
              : item
          ),
        }));
      },
      
      deleteItem: (id) => {
        set((state) => ({
          items: state.items.filter((item) => item.id !== id),
        }));
      },
      
      getItem: (id) => {
        return get().items.find((item) => item.id === id);
      },
      
      updateStock: (id, quantity, operation) => {
        set((state) => ({
          items: state.items.map((item) => {
            if (item.id === id) {
              let newQuantity = item.stockQuantity;
              switch (operation) {
                case 'add':
                  newQuantity += quantity;
                  break;
                case 'subtract':
                  newQuantity = Math.max(0, newQuantity - quantity);
                  break;
                case 'set':
                  newQuantity = Math.max(0, quantity);
                  break;
              }
              return {
                ...item,
                stockQuantity: newQuantity,
                updatedAt: new Date().toISOString(),
              };
            }
            return item;
          }),
        }));
      },
      
      addCategory: (categoryData) => {
        const newCategory: Category = {
          ...categoryData,
          id: Date.now().toString(),
        };
        set((state) => ({ categories: [...state.categories, newCategory] }));
      },
      
      updateCategory: (id, updates) => {
        set((state) => ({
          categories: state.categories.map((category) =>
            category.id === id ? { ...category, ...updates } : category
          ),
        }));
      },
      
      deleteCategory: (id) => {
        set((state) => ({
          categories: state.categories.filter((category) => category.id !== id),
        }));
      },
      
      addUnit: (unitData) => {
        const newUnit: Unit = {
          ...unitData,
          id: Date.now().toString(),
        };
        set((state) => ({ units: [...state.units, newUnit] }));
      },
      
      updateUnit: (id, updates) => {
        set((state) => ({
          units: state.units.map((unit) =>
            unit.id === id ? { ...unit, ...updates } : unit
          ),
        }));
      },
      
      deleteUnit: (id) => {
        set((state) => ({
          units: state.units.filter((unit) => unit.id !== id),
        }));
      },
      
      getLowStockItems: () => {
        const { items } = get();
        return items.filter((item) => item.stockQuantity <= item.lowStockThreshold);
      },
      
      getExpiredItems: () => {
        const { items } = get();
        const today = new Date();
        return items.filter((item) => {
          const expiryDate = new Date(item.expiryDate);
          return expiryDate < today;
        });
      },
      
      getNearExpiryItems: (days = 30) => {
        const { items } = get();
        const today = new Date();
        const futureDate = new Date(today.getTime() + days * 24 * 60 * 60 * 1000);
        
        return items.filter((item) => {
          const expiryDate = new Date(item.expiryDate);
          return expiryDate >= today && expiryDate <= futureDate;
        });
      },
      
      getLowStockCount: () => get().getLowStockItems().length,
      getExpiredCount: () => get().getExpiredItems().length,
      getNearExpiryCount: (days = 30) => get().getNearExpiryItems(days).length,
      
      getTotalValue: () => {
        const { items } = get();
        return items.reduce((total, item) => total + (item.sellPrice * item.stockQuantity), 0);
      },
      
      setDefaultLowStockThreshold: (threshold) => {
        set({ defaultLowStockThreshold: threshold });
      },
    }),
    {
      name: 'inventory-storage',
    }
  )
);
