import { create } from 'zustand';
import { persist } from 'zustand/middleware';

export interface Customer {
  id: string;
  name: string;
  mobile: string;
  address: string;
  email?: string;
  medicalSummary?: string;
  createdAt: string;
  updatedAt: string;
}

export interface Visit {
  id: string;
  customerId: string;
  visitDate: string;
  nextVisitDate?: string;
  notes: string;
  prescription?: string;
  status: 'scheduled' | 'completed' | 'cancelled';
  reminderSent: boolean;
  createdAt: string;
  updatedAt: string;
}

export interface Appointment {
  id: string;
  customerId: string;
  appointmentDate: string;
  appointmentTime: string;
  purpose: string;
  status: 'scheduled' | 'completed' | 'cancelled';
  notes?: string;
  createdAt: string;
  updatedAt: string;
}

interface CustomerState {
  customers: Customer[];
  visits: Visit[];
  appointments: Appointment[];
  
  // Customer operations
  addCustomer: (customer: Omit<Customer, 'id' | 'createdAt' | 'updatedAt'>) => void;
  updateCustomer: (id: string, updates: Partial<Customer>) => void;
  deleteCustomer: (id: string) => void;
  getCustomer: (id: string) => Customer | undefined;
  searchCustomers: (query: string) => Customer[];
  
  // Visit operations
  addVisit: (visit: Omit<Visit, 'id' | 'createdAt' | 'updatedAt'>) => void;
  updateVisit: (id: string, updates: Partial<Visit>) => void;
  deleteVisit: (id: string) => void;
  getCustomerVisits: (customerId: string) => Visit[];
  markVisitCompleted: (id: string) => void;
  
  // Appointment operations
  addAppointment: (appointment: Omit<Appointment, 'id' | 'createdAt' | 'updatedAt'>) => void;
  updateAppointment: (id: string, updates: Partial<Appointment>) => void;
  deleteAppointment: (id: string) => void;
  getCustomerAppointments: (customerId: string) => Appointment[];
  
  // Analytics
  getTodayVisits: () => Visit[];
  getUpcomingVisits: (days?: number) => Visit[];
  getTodayAppointments: () => Appointment[];
  getUpcomingAppointments: (days?: number) => Appointment[];
  getTodayVisitsCount: () => number;
  getUpcomingVisitsCount: (days?: number) => number;
  getCustomerStats: () => {
    totalCustomers: number;
    newThisMonth: number;
    activeCustomers: number;
  };
}

export const useCustomerStore = create<CustomerState>()(
  persist(
    (set, get) => ({
      customers: [],
      visits: [],
      appointments: [],
      
      addCustomer: (customerData) => {
        const newCustomer: Customer = {
          ...customerData,
          id: Date.now().toString(),
          createdAt: new Date().toISOString(),
          updatedAt: new Date().toISOString(),
        };
        set((state) => ({ customers: [...state.customers, newCustomer] }));
      },
      
      updateCustomer: (id, updates) => {
        set((state) => ({
          customers: state.customers.map((customer) =>
            customer.id === id
              ? { ...customer, ...updates, updatedAt: new Date().toISOString() }
              : customer
          ),
        }));
      },
      
      deleteCustomer: (id) => {
        set((state) => ({
          customers: state.customers.filter((customer) => customer.id !== id),
          visits: state.visits.filter((visit) => visit.customerId !== id),
          appointments: state.appointments.filter((appointment) => appointment.customerId !== id),
        }));
      },
      
      getCustomer: (id) => {
        return get().customers.find((customer) => customer.id === id);
      },
      
      searchCustomers: (query) => {
        const { customers } = get();
        const lowercaseQuery = query.toLowerCase();
        return customers.filter(
          (customer) =>
            customer.name.toLowerCase().includes(lowercaseQuery) ||
            customer.mobile.includes(query) ||
            customer.address.toLowerCase().includes(lowercaseQuery)
        );
      },
      
      addVisit: (visitData) => {
        const newVisit: Visit = {
          ...visitData,
          id: Date.now().toString(),
          createdAt: new Date().toISOString(),
          updatedAt: new Date().toISOString(),
        };
        set((state) => ({ visits: [...state.visits, newVisit] }));
      },
      
      updateVisit: (id, updates) => {
        set((state) => ({
          visits: state.visits.map((visit) =>
            visit.id === id
              ? { ...visit, ...updates, updatedAt: new Date().toISOString() }
              : visit
          ),
        }));
      },
      
      deleteVisit: (id) => {
        set((state) => ({
          visits: state.visits.filter((visit) => visit.id !== id),
        }));
      },
      
      getCustomerVisits: (customerId) => {
        const { visits } = get();
        return visits
          .filter((visit) => visit.customerId === customerId)
          .sort((a, b) => new Date(b.visitDate).getTime() - new Date(a.visitDate).getTime());
      },
      
      markVisitCompleted: (id) => {
        get().updateVisit(id, { status: 'completed' });
      },
      
      addAppointment: (appointmentData) => {
        const newAppointment: Appointment = {
          ...appointmentData,
          id: Date.now().toString(),
          createdAt: new Date().toISOString(),
          updatedAt: new Date().toISOString(),
        };
        set((state) => ({ appointments: [...state.appointments, newAppointment] }));
      },
      
      updateAppointment: (id, updates) => {
        set((state) => ({
          appointments: state.appointments.map((appointment) =>
            appointment.id === id
              ? { ...appointment, ...updates, updatedAt: new Date().toISOString() }
              : appointment
          ),
        }));
      },
      
      deleteAppointment: (id) => {
        set((state) => ({
          appointments: state.appointments.filter((appointment) => appointment.id !== id),
        }));
      },
      
      getCustomerAppointments: (customerId) => {
        const { appointments } = get();
        return appointments
          .filter((appointment) => appointment.customerId === customerId)
          .sort((a, b) => new Date(a.appointmentDate).getTime() - new Date(b.appointmentDate).getTime());
      },
      
      getTodayVisits: () => {
        const { visits } = get();
        const today = new Date().toISOString().split('T')[0];
        return visits.filter((visit) => visit.visitDate.startsWith(today));
      },
      
      getUpcomingVisits: (days = 7) => {
        const { visits } = get();
        const today = new Date();
        const futureDate = new Date(today.getTime() + days * 24 * 60 * 60 * 1000);
        
        return visits.filter((visit) => {
          if (!visit.nextVisitDate) return false;
          const visitDate = new Date(visit.nextVisitDate);
          return visitDate >= today && visitDate <= futureDate;
        });
      },
      
      getTodayAppointments: () => {
        const { appointments } = get();
        const today = new Date().toISOString().split('T')[0];
        return appointments.filter((appointment) => 
          appointment.appointmentDate.startsWith(today) && 
          appointment.status === 'scheduled'
        );
      },
      
      getUpcomingAppointments: (days = 7) => {
        const { appointments } = get();
        const today = new Date();
        const futureDate = new Date(today.getTime() + days * 24 * 60 * 60 * 1000);
        
        return appointments.filter((appointment) => {
          const appointmentDate = new Date(appointment.appointmentDate);
          return appointmentDate >= today && 
                 appointmentDate <= futureDate && 
                 appointment.status === 'scheduled';
        });
      },
      
      getTodayVisitsCount: () => get().getTodayVisits().length,
      getUpcomingVisitsCount: (days = 7) => get().getUpcomingVisits(days).length,
      
      getCustomerStats: () => {
        const { customers } = get();
        const now = new Date();
        const thisMonth = new Date(now.getFullYear(), now.getMonth(), 1);
        
        const newThisMonth = customers.filter(
          (customer) => new Date(customer.createdAt) >= thisMonth
        ).length;
        
        // Active customers are those with visits in the last 3 months
        const threeMonthsAgo = new Date(now.getTime() - 90 * 24 * 60 * 60 * 1000);
        const { visits } = get();
        const activeCustomerIds = new Set(
          visits
            .filter((visit) => new Date(visit.visitDate) >= threeMonthsAgo)
            .map((visit) => visit.customerId)
        );
        
        return {
          totalCustomers: customers.length,
          newThisMonth,
          activeCustomers: activeCustomerIds.size,
        };
      },
    }),
    {
      name: 'customer-storage',
    }
  )
);
