"use client";

import { motion } from "framer-motion";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import { useInventoryStore } from "@/stores/inventory-store";
import { useCustomerStore } from "@/stores/customer-store";
import { useBillingStore } from "@/stores/billing-store";

export function DashboardOverview() {
  const { 
    items, 
    getLowStockCount, 
    getExpiredCount, 
    getNearExpiryCount, 
    getTotalValue,
    getLowStockItems,
    getExpiredItems,
    getNearExpiryItems
  } = useInventoryStore();
  
  const { 
    customers, 
    getTodayVisitsCount, 
    getUpcomingVisitsCount,
    getCustomerStats,
    getTodayVisits,
    getUpcomingVisits
  } = useCustomerStore();
  
  const { 
    getTodaySales, 
    getThisMonthSales, 
    getPendingInvoicesCount,
    getRecentInvoices 
  } = useBillingStore();

  const customerStats = getCustomerStats();
  const todaySales = getTodaySales();
  const monthSales = getThisMonthSales();
  const recentInvoices = getRecentInvoices(5);

  const statsCards = [
    {
      title: "Total Inventory Value",
      value: `₹${getTotalValue().toLocaleString()}`,
      description: `${items.length} items in stock`,
      icon: "💰",
      color: "from-green-500 to-emerald-600"
    },
    {
      title: "Today's Sales",
      value: `₹${todaySales.toLocaleString()}`,
      description: "Revenue generated today",
      icon: "📈",
      color: "from-blue-500 to-cyan-600"
    },
    {
      title: "Total Customers",
      value: customerStats.totalCustomers.toString(),
      description: `${customerStats.newThisMonth} new this month`,
      icon: "👥",
      color: "from-purple-500 to-violet-600"
    },
    {
      title: "Pending Invoices",
      value: getPendingInvoicesCount().toString(),
      description: "Awaiting payment",
      icon: "📋",
      color: "from-orange-500 to-amber-600"
    }
  ];

  const alertCards = [
    {
      title: "Low Stock Items",
      count: getLowStockCount(),
      items: getLowStockItems().slice(0, 3),
      color: "yellow",
      icon: "⚠️"
    },
    {
      title: "Expired Items",
      count: getExpiredCount(),
      items: getExpiredItems().slice(0, 3),
      color: "red",
      icon: "🚨"
    },
    {
      title: "Near Expiry",
      count: getNearExpiryCount(),
      items: getNearExpiryItems().slice(0, 3),
      color: "orange",
      icon: "⏰"
    }
  ];

  return (
    <div className="space-y-6">
      {/* Welcome Section */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="bg-gradient-to-r from-blue-600 to-indigo-700 rounded-lg p-6 text-white"
      >
        <h1 className="text-2xl font-bold mb-2">Welcome back! 👋</h1>
        <p className="text-blue-100">
          Here's what's happening with your business today.
        </p>
      </motion.div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {statsCards.map((stat, index) => (
          <motion.div
            key={stat.title}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
          >
            <Card className="relative overflow-hidden">
              <div className={`absolute inset-0 bg-gradient-to-r ${stat.color} opacity-5`} />
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium text-gray-600 dark:text-gray-400">
                  {stat.title}
                </CardTitle>
                <span className="text-2xl">{stat.icon}</span>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{stat.value}</div>
                <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                  {stat.description}
                </p>
              </CardContent>
            </Card>
          </motion.div>
        ))}
      </div>

      {/* Alerts Section */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {alertCards.map((alert, index) => (
          <motion.div
            key={alert.title}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.4 + index * 0.1 }}
          >
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center justify-between">
                  <span className="flex items-center space-x-2">
                    <span>{alert.icon}</span>
                    <span>{alert.title}</span>
                  </span>
                  {alert.count > 0 && (
                    <Badge 
                      variant={alert.color === 'red' ? 'destructive' : 'secondary'}
                      className={
                        alert.color === 'yellow' ? 'bg-yellow-100 text-yellow-800' :
                        alert.color === 'orange' ? 'bg-orange-100 text-orange-800' : ''
                      }
                    >
                      {alert.count}
                    </Badge>
                  )}
                </CardTitle>
              </CardHeader>
              <CardContent>
                {alert.count === 0 ? (
                  <p className="text-sm text-gray-500">All good! 👍</p>
                ) : (
                  <div className="space-y-2">
                    {alert.items.map((item) => (
                      <div key={item.id} className="flex justify-between items-center text-sm">
                        <span className="truncate">{item.name}</span>
                        <span className="text-gray-500">
                          {alert.title === 'Low Stock Items' ? `${item.stockQuantity} left` :
                           alert.title === 'Expired Items' ? 'Expired' :
                           'Expiring soon'}
                        </span>
                      </div>
                    ))}
                    {alert.count > 3 && (
                      <p className="text-xs text-gray-500">
                        +{alert.count - 3} more items
                      </p>
                    )}
                  </div>
                )}
              </CardContent>
            </Card>
          </motion.div>
        ))}
      </div>

      {/* Today's Activity */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Today's Visits */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.7 }}
        >
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center space-x-2">
                <span>📅</span>
                <span>Today's Schedule</span>
              </CardTitle>
              <CardDescription>
                {getTodayVisitsCount()} visits scheduled for today
              </CardDescription>
            </CardHeader>
            <CardContent>
              {getTodayVisitsCount() === 0 ? (
                <p className="text-sm text-gray-500">No visits scheduled today</p>
              ) : (
                <div className="space-y-3">
                  {getTodayVisits().slice(0, 3).map((visit) => {
                    const customer = customers.find(c => c.id === visit.customerId);
                    return (
                      <div key={visit.id} className="flex items-center justify-between p-2 bg-gray-50 dark:bg-gray-800 rounded">
                        <div>
                          <p className="font-medium">{customer?.name}</p>
                          <p className="text-sm text-gray-500">{visit.notes}</p>
                        </div>
                        <Badge variant={visit.status === 'completed' ? 'default' : 'secondary'}>
                          {visit.status}
                        </Badge>
                      </div>
                    );
                  })}
                </div>
              )}
            </CardContent>
          </Card>
        </motion.div>

        {/* Recent Sales */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.8 }}
        >
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center space-x-2">
                <span>💳</span>
                <span>Recent Sales</span>
              </CardTitle>
              <CardDescription>
                Latest transactions
              </CardDescription>
            </CardHeader>
            <CardContent>
              {recentInvoices.length === 0 ? (
                <p className="text-sm text-gray-500">No recent sales</p>
              ) : (
                <div className="space-y-3">
                  {recentInvoices.map((invoice) => {
                    const customer = customers.find(c => c.id === invoice.customerId);
                    return (
                      <div key={invoice.id} className="flex items-center justify-between p-2 bg-gray-50 dark:bg-gray-800 rounded">
                        <div>
                          <p className="font-medium">{customer?.name || 'Walk-in Customer'}</p>
                          <p className="text-sm text-gray-500">
                            {new Date(invoice.createdAt).toLocaleDateString()}
                          </p>
                        </div>
                        <div className="text-right">
                          <p className="font-medium">₹{invoice.total.toLocaleString()}</p>
                          <Badge variant={invoice.paymentStatus === 'paid' ? 'default' : 'destructive'}>
                            {invoice.paymentStatus}
                          </Badge>
                        </div>
                      </div>
                    );
                  })}
                </div>
              )}
            </CardContent>
          </Card>
        </motion.div>
      </div>

      {/* Quick Actions */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.9 }}
      >
        <Card>
          <CardHeader>
            <CardTitle>Quick Actions</CardTitle>
            <CardDescription>Frequently used features</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <Button variant="outline" className="h-20 flex flex-col space-y-2">
                <span className="text-2xl">📦</span>
                <span>Add Item</span>
              </Button>
              <Button variant="outline" className="h-20 flex flex-col space-y-2">
                <span className="text-2xl">🧾</span>
                <span>New Invoice</span>
              </Button>
              <Button variant="outline" className="h-20 flex flex-col space-y-2">
                <span className="text-2xl">👤</span>
                <span>Add Customer</span>
              </Button>
              <Button variant="outline" className="h-20 flex flex-col space-y-2">
                <span className="text-2xl">📊</span>
                <span>View Reports</span>
              </Button>
            </div>
          </CardContent>
        </Card>
      </motion.div>
    </div>
  );
}
