"use client";

import { motion } from "framer-motion";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Separator } from "@/components/ui/separator";
import { Badge } from "@/components/ui/badge";
import { ActivePage } from "@/components/dashboard/dashboard";
import { useInventoryStore } from "@/stores/inventory-store";
import { useCustomerStore } from "@/stores/customer-store";

interface SidebarProps {
  activePage: ActivePage;
  setActivePage: (page: ActivePage) => void;
  isOpen: boolean;
  setIsOpen: (open: boolean) => void;
}

const menuItems = [
  { id: 'dashboard', label: 'Dashboard', icon: '📊' },
  { id: 'inventory', label: 'Inventory', icon: '📦' },
  { id: 'billing', label: 'Billing', icon: '🧾' },
  { id: 'customers', label: 'Customers', icon: '👥' },
  { id: 'reports', label: 'Reports', icon: '📈' },
  { id: 'settings', label: 'Settings', icon: '⚙️' },
];

export function Sidebar({ activePage, setActivePage, isOpen, setIsOpen }: SidebarProps) {
  const { getLowStockCount, getExpiredCount, getNearExpiryCount } = useInventoryStore();
  const { getTodayVisitsCount, getUpcomingVisitsCount } = useCustomerStore();

  const getNotificationCount = (pageId: string) => {
    switch (pageId) {
      case 'inventory':
        return getLowStockCount() + getExpiredCount() + getNearExpiryCount();
      case 'customers':
        return getTodayVisitsCount() + getUpcomingVisitsCount();
      default:
        return 0;
    }
  };

  return (
    <motion.div
      initial={{ x: -300 }}
      animate={{ x: 0 }}
      className={`fixed left-0 top-0 h-full bg-white dark:bg-gray-800 border-r border-gray-200 dark:border-gray-700 shadow-lg z-30 transition-all duration-300 ${
        isOpen ? 'w-64' : 'w-16'
      }`}
    >
      <div className="flex flex-col h-full">
        {/* Logo */}
        <div className="p-4 border-b border-gray-200 dark:border-gray-700">
          <div className="flex items-center space-x-3">
            <div className="w-8 h-8 bg-gradient-to-r from-blue-500 to-indigo-600 rounded-lg flex items-center justify-center">
              <span className="text-white text-sm font-bold">SM</span>
            </div>
            {isOpen && (
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.1 }}
              >
                <h1 className="text-lg font-bold bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent">
                  ShopManager
                </h1>
                <p className="text-xs text-gray-500 dark:text-gray-400">Pro</p>
              </motion.div>
            )}
          </div>
        </div>

        {/* Navigation */}
        <ScrollArea className="flex-1 p-2">
          <div className="space-y-1">
            {menuItems.map((item) => {
              const notificationCount = getNotificationCount(item.id);
              const isActive = activePage === item.id;
              
              return (
                <Button
                  key={item.id}
                  variant={isActive ? "default" : "ghost"}
                  className={`w-full justify-start h-12 transition-all duration-200 ${
                    isActive 
                      ? "bg-gradient-to-r from-blue-500 to-indigo-600 text-white shadow-md" 
                      : "hover:bg-gray-100 dark:hover:bg-gray-700"
                  } ${!isOpen && 'px-3'}`}
                  onClick={() => setActivePage(item.id as ActivePage)}
                >
                  <span className="text-lg mr-3">{item.icon}</span>
                  {isOpen && (
                    <motion.div
                      initial={{ opacity: 0 }}
                      animate={{ opacity: 1 }}
                      className="flex items-center justify-between w-full"
                    >
                      <span className="font-medium">{item.label}</span>
                      {notificationCount > 0 && (
                        <Badge variant="destructive" className="ml-2 h-5 min-w-5 text-xs">
                          {notificationCount > 99 ? '99+' : notificationCount}
                        </Badge>
                      )}
                    </motion.div>
                  )}
                </Button>
              );
            })}
          </div>
        </ScrollArea>

        {/* Footer */}
        <div className="p-4 border-t border-gray-200 dark:border-gray-700">
          {isOpen && (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              className="text-center"
            >
              <p className="text-xs text-gray-500 dark:text-gray-400">
                Version 1.0.0
              </p>
              <p className="text-xs text-gray-400 dark:text-gray-500">
                © 2024 ShopManager Pro
              </p>
            </motion.div>
          )}
        </div>
      </div>
    </motion.div>
  );
}
