import { create } from 'zustand';
import { persist } from 'zustand/middleware';

export interface InvoiceItem {
  id: string;
  inventoryItemId: string;
  name: string;
  quantity: number;
  unitPrice: number;
  discount: number;
  taxRate: number;
  total: number;
}

export interface Invoice {
  id: string;
  invoiceNumber: string;
  customerId?: string;
  customerName: string;
  customerMobile?: string;
  customerAddress?: string;
  items: InvoiceItem[];
  subtotal: number;
  totalDiscount: number;
  totalTax: number;
  total: number;
  paymentMethod: 'cash' | 'card' | 'upi' | 'credit';
  paymentStatus: 'paid' | 'pending' | 'overdue';
  notes?: string;
  createdAt: string;
  updatedAt: string;
  dueDate?: string;
}

export interface PaymentReminder {
  id: string;
  invoiceId: string;
  reminderDate: string;
  status: 'pending' | 'sent' | 'completed';
  notes?: string;
}

interface BillingState {
  invoices: Invoice[];
  paymentReminders: PaymentReminder[];
  invoiceCounter: number;
  
  // Invoice operations
  createInvoice: (invoice: Omit<Invoice, 'id' | 'invoiceNumber' | 'createdAt' | 'updatedAt'>) => string;
  updateInvoice: (id: string, updates: Partial<Invoice>) => void;
  deleteInvoice: (id: string) => void;
  getInvoice: (id: string) => Invoice | undefined;
  markInvoiceAsPaid: (id: string) => void;
  markInvoiceAsReturn: (id: string) => void;
  
  // Payment reminders
  addPaymentReminder: (reminder: Omit<PaymentReminder, 'id'>) => void;
  updatePaymentReminder: (id: string, updates: Partial<PaymentReminder>) => void;
  deletePaymentReminder: (id: string) => void;
  
  // Analytics
  getTodaySales: () => number;
  getThisMonthSales: () => number;
  getThisYearSales: () => number;
  getSalesByDateRange: (startDate: string, endDate: string) => number;
  getPendingInvoices: () => Invoice[];
  getPendingInvoicesCount: () => number;
  getOverdueInvoices: () => Invoice[];
  getRecentInvoices: (limit?: number) => Invoice[];
  getSalesReport: (startDate: string, endDate: string) => {
    totalSales: number;
    totalInvoices: number;
    averageInvoiceValue: number;
    topSellingItems: { name: string; quantity: number; revenue: number }[];
  };
  
  // Utilities
  generateInvoiceNumber: () => string;
  calculateInvoiceTotals: (items: InvoiceItem[]) => {
    subtotal: number;
    totalDiscount: number;
    totalTax: number;
    total: number;
  };
}

export const useBillingStore = create<BillingState>()(
  persist(
    (set, get) => ({
      invoices: [],
      paymentReminders: [],
      invoiceCounter: 1,
      
      createInvoice: (invoiceData) => {
        const invoiceNumber = get().generateInvoiceNumber();
        const newInvoice: Invoice = {
          ...invoiceData,
          id: Date.now().toString(),
          invoiceNumber,
          createdAt: new Date().toISOString(),
          updatedAt: new Date().toISOString(),
        };
        
        set((state) => ({
          invoices: [...state.invoices, newInvoice],
          invoiceCounter: state.invoiceCounter + 1,
        }));
        
        return newInvoice.id;
      },
      
      updateInvoice: (id, updates) => {
        set((state) => ({
          invoices: state.invoices.map((invoice) =>
            invoice.id === id
              ? { ...invoice, ...updates, updatedAt: new Date().toISOString() }
              : invoice
          ),
        }));
      },
      
      deleteInvoice: (id) => {
        set((state) => ({
          invoices: state.invoices.filter((invoice) => invoice.id !== id),
          paymentReminders: state.paymentReminders.filter((reminder) => reminder.invoiceId !== id),
        }));
      },
      
      getInvoice: (id) => {
        return get().invoices.find((invoice) => invoice.id === id);
      },
      
      markInvoiceAsPaid: (id) => {
        get().updateInvoice(id, { paymentStatus: 'paid' });
      },
      
      markInvoiceAsReturn: (id) => {
        const invoice = get().getInvoice(id);
        if (invoice) {
          // Update inventory stock back
          const { updateStock } = require('@/stores/inventory-store').useInventoryStore.getState();
          invoice.items.forEach((item) => {
            updateStock(item.inventoryItemId, item.quantity, 'add');
          });
          
          get().deleteInvoice(id);
        }
      },
      
      addPaymentReminder: (reminderData) => {
        const newReminder: PaymentReminder = {
          ...reminderData,
          id: Date.now().toString(),
        };
        set((state) => ({
          paymentReminders: [...state.paymentReminders, newReminder],
        }));
      },
      
      updatePaymentReminder: (id, updates) => {
        set((state) => ({
          paymentReminders: state.paymentReminders.map((reminder) =>
            reminder.id === id ? { ...reminder, ...updates } : reminder
          ),
        }));
      },
      
      deletePaymentReminder: (id) => {
        set((state) => ({
          paymentReminders: state.paymentReminders.filter((reminder) => reminder.id !== id),
        }));
      },
      
      getTodaySales: () => {
        const { invoices } = get();
        const today = new Date().toISOString().split('T')[0];
        return invoices
          .filter((invoice) => 
            invoice.createdAt.startsWith(today) && 
            invoice.paymentStatus === 'paid'
          )
          .reduce((total, invoice) => total + invoice.total, 0);
      },
      
      getThisMonthSales: () => {
        const { invoices } = get();
        const now = new Date();
        const thisMonth = new Date(now.getFullYear(), now.getMonth(), 1);
        return invoices
          .filter((invoice) => 
            new Date(invoice.createdAt) >= thisMonth && 
            invoice.paymentStatus === 'paid'
          )
          .reduce((total, invoice) => total + invoice.total, 0);
      },
      
      getThisYearSales: () => {
        const { invoices } = get();
        const now = new Date();
        const thisYear = new Date(now.getFullYear(), 0, 1);
        return invoices
          .filter((invoice) => 
            new Date(invoice.createdAt) >= thisYear && 
            invoice.paymentStatus === 'paid'
          )
          .reduce((total, invoice) => total + invoice.total, 0);
      },
      
      getSalesByDateRange: (startDate, endDate) => {
        const { invoices } = get();
        const start = new Date(startDate);
        const end = new Date(endDate);
        return invoices
          .filter((invoice) => {
            const invoiceDate = new Date(invoice.createdAt);
            return invoiceDate >= start && 
                   invoiceDate <= end && 
                   invoice.paymentStatus === 'paid';
          })
          .reduce((total, invoice) => total + invoice.total, 0);
      },
      
      getPendingInvoices: () => {
        const { invoices } = get();
        return invoices.filter((invoice) => invoice.paymentStatus === 'pending');
      },
      
      getPendingInvoicesCount: () => get().getPendingInvoices().length,
      
      getOverdueInvoices: () => {
        const { invoices } = get();
        const today = new Date();
        return invoices.filter((invoice) => {
          if (invoice.paymentStatus !== 'pending' || !invoice.dueDate) return false;
          return new Date(invoice.dueDate) < today;
        });
      },
      
      getRecentInvoices: (limit = 10) => {
        const { invoices } = get();
        return invoices
          .sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime())
          .slice(0, limit);
      },
      
      getSalesReport: (startDate, endDate) => {
        const { invoices } = get();
        const start = new Date(startDate);
        const end = new Date(endDate);
        
        const filteredInvoices = invoices.filter((invoice) => {
          const invoiceDate = new Date(invoice.createdAt);
          return invoiceDate >= start && 
                 invoiceDate <= end && 
                 invoice.paymentStatus === 'paid';
        });
        
        const totalSales = filteredInvoices.reduce((total, invoice) => total + invoice.total, 0);
        const totalInvoices = filteredInvoices.length;
        const averageInvoiceValue = totalInvoices > 0 ? totalSales / totalInvoices : 0;
        
        // Calculate top selling items
        const itemSales: { [key: string]: { quantity: number; revenue: number; name: string } } = {};
        
        filteredInvoices.forEach((invoice) => {
          invoice.items.forEach((item) => {
            if (!itemSales[item.inventoryItemId]) {
              itemSales[item.inventoryItemId] = {
                quantity: 0,
                revenue: 0,
                name: item.name,
              };
            }
            itemSales[item.inventoryItemId].quantity += item.quantity;
            itemSales[item.inventoryItemId].revenue += item.total;
          });
        });
        
        const topSellingItems = Object.values(itemSales)
          .sort((a, b) => b.revenue - a.revenue)
          .slice(0, 10);
        
        return {
          totalSales,
          totalInvoices,
          averageInvoiceValue,
          topSellingItems,
        };
      },
      
      generateInvoiceNumber: () => {
        const { invoiceCounter } = get();
        const today = new Date();
        const year = today.getFullYear().toString().slice(-2);
        const month = (today.getMonth() + 1).toString().padStart(2, '0');
        return `INV${year}${month}${invoiceCounter.toString().padStart(4, '0')}`;
      },
      
      calculateInvoiceTotals: (items) => {
        const subtotal = items.reduce((sum, item) => sum + (item.quantity * item.unitPrice), 0);
        const totalDiscount = items.reduce((sum, item) => sum + item.discount, 0);
        const totalTax = items.reduce((sum, item) => sum + (item.quantity * item.unitPrice * item.taxRate / 100), 0);
        const total = subtotal - totalDiscount + totalTax;
        
        return {
          subtotal,
          totalDiscount,
          totalTax,
          total,
        };
      },
    }),
    {
      name: 'billing-storage',
    }
  )
);
